* {
  box-sizing: border-box;
}

body {
}

nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #444;
  font-size: 1.2rem;
  h2{
    margin: .3rem 0;
  }
  li {
    list-style: none;
    padding-right: 1rem;
  }
  a{
    color: #444;
    text-decoration: none;
    // font-size: 1rem;
    &:hover {
      text-decoration: underline;
    }
  }
  div {
    display: flex;
  }
}

form {
  display: inline;
}

hr {
  border: none;
  border-bottom: 1px solid #ddd;
}